import $ from "jquery";

// ,   DOMContentLoaded
$(document).ready(function() {
    //   
    $('.list-item:first').hover(function() {
        //   hover    active
        $(this).toggleClass('active');
    });
    //         ""
    $('.list-item:eq(2)').hover(function() {
        $(this).toggleClass('active');
    }).on('click', function() {
        $('.image:even').fadeToggle('slow');
    });
    //        ""
    $('.list-item:eq(4)').hover(function() {
        $(this).toggleClass('active');
    }).on('click', function() {
        $('.image:odd').animate({
            opacity: 'toggle',
            height: 'toggle'
        }, 2000);
    });
});